.slider-textures {
  position: relative;
  padding-top: 50px;

  &__wrapper {
    display: flex;
    margin-bottom: 73px;
    position: relative;
  }

  &__card {
    width: 291px;
    height: 347px;
    background-color: $milling-card;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 0;

    &:hover img{
      top: -50px;
    }
  }

  &__photo {
    width: 230px;
    height: 182px;
    position: relative;
    top: -40px;
    transition: top 0.3s cubic-bezier(0.5,0,0.5,2);
  }

  &__category {
    font-size: 14px;
    color: $milling-category-color;
    font-weight: 400;
    margin: 10px 0 0 0;
    position: relative;
  }

  &__name {
    font-size: 20px;
    margin: 10px 0 0 0;
    padding: 0 31px;
    text-align: center;
  }

  &__about {
    color: $milling-category-color;
    font-size: 11px;
    margin-top: 40px;
    position: absolute;
    bottom: 20px;
  }
}
